<script>
	import mapbox from 'mapbox-gl';
	import { bbox } from '@turf/turf';
	import { getContext } from 'svelte';
	import { getColor } from './utils.js';
	
	export let id;
	export let source;
	export let sourceLayer = null;
	export let type;
	export let layout = {};
	export let paint = {};
	export let data = null;
	export let colors = null;
	export let selected = null;
	export let hovered = null;
	export let highlighted = null;
	export let click = false;
	export let hover = false;
	export let highlight = false;
	export let order = 'boundary_country';
	
	export let bounds = [[-5.816, 49.864], [1.863, 55.872]]; // England & Wales bounding box
	
	const { getMap } = getContext('map');
	const map = getMap();
	
	let selectedPrev = null;
	let highlightedPrev = null;
	
	if (map.getLayer(id)) {
    map.removeLayer(id);
	}
	
	let options = {
		'id': id,
		'type': type,
		'source': source,
		'paint': paint,
		'layout': layout
	};
	
	if (sourceLayer) {
		options['source-layer'] = sourceLayer;
	}
	
	map.addLayer(options, order);
		
	$: if (type == 'fill' && data && map.getLayer(id)) {
		for (let i in data) {
			if (data[i].code) {
				map.setFeatureState({
					source: source,
					sourceLayer: sourceLayer,
					id: data[i].code
				}, {
					color: colors[data[i].value - 1]
				});
			}
		}
	}
	
	if (click) {
		map.on('click', id, (e) => {
			e.preventDefault();
      if (e.features.length > 0) {
        if (selected) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: selected },
            { selected: false }
          );
        }
        selected = e.features[0].id;
				
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: selected },
          { selected: true }
        );

				// Fit to map feature (only works for geojson layers)
				let layerSource = map.getSource(source);
				if (layerSource._options.type == 'geojson') {
					let promoteId = layerSource._options.promoteId;
					let feature = layerSource._data.features.filter(f => f.properties[promoteId] == selected)[0];
					let box = bbox(feature.geometry);
					map.fitBounds(box, {padding: 20});
				}
				
				selectedPrev = selected;
      }
    });
		
		map.on('click', (e) => {
			if (e.defaultPrevented === false) {
				if (selected) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: selected },
            { selected: false }
          );
        }
				map.fitBounds(bounds);
				
				selectedPrev = null;
				selected = null;
			}
		});
	}
	
	if (hover) {
		map.on('mousemove', id, (e) => {
      if (e.features.length > 0) {
        if (hovered) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: hovered },
            { hovered: false }
          );
        }
				hovered = e.features[0].id;
				
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: hovered },
          { hovered: true }
        );

        // Change the cursor style as a UI indicator.
        map.getCanvas().style.cursor = 'pointer';
      }
		});

		map.on('mouseleave', id, (e) => {
			if (hovered) {
				map.setFeatureState(
					{ source: source, sourceLayer: sourceLayer, id: hovered },
					{ hovered: false }
				);
				hovered = null;
			}
		});
	}

	$: if (selected != selectedPrev) {

		if (selectedPrev) {
			map.setFeatureState(
				{ source: source, sourceLayer: sourceLayer, id: selectedPrev },
				{ selected: false }
			);
		}

		if (selected) {
			map.setFeatureState(
				{ source: source, sourceLayer: sourceLayer, id: selected },
				{ selected: true }
			);
			
			// Fit to map feature (only works for geojson layers)
			let layerSource = map.getSource(source);
			if (layerSource._options.type == 'geojson') {
				let promoteId = layerSource._options.promoteId;
				let feature = layerSource._data.features.filter(f => f.properties[promoteId] == selected)[0];
				let box = bbox(feature.geometry);
				map.fitBounds(box, {padding: 20});
			}
		} else {
			map.fitBounds(bounds);
		}
		
		selectedPrev = selected;
	}
	
	$: if (highlight) {
		if (highlighted) {
			if (highlightedPrev) {
				for (let i in highlightedPrev) {
					map.setFeatureState(
						{ source: source, sourceLayer: sourceLayer, id: highlightedPrev[i] },
						{ highlighted: false }
					);
				}
			}
			highlightedPrev = highlighted;
			for (let i in highlighted) {
				map.setFeatureState(
					{ source: source, sourceLayer: sourceLayer, id: highlighted[i] },
					{ highlighted: true }
				);
			}
		}
	}
	
</script>