<script>
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import Panel from './Panel.svelte';
	import Select from './Select.svelte';
	import ColChart from './ColChart.svelte';
	import { getTopo, getData } from './utils.js';
	
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-dark.json';
	const topojson = {
		url: 'https://raw.githubusercontent.com/ONSvisual/topojson_boundaries/master/geogLA2019EW.json',
		layer: 'la2019EW',
		id: 'AREACD'
	};
	const vector = {
		url: 'https://cdn.ons.gov.uk/maptiles/t18/tiles/{z}/{x}/{y}.pbf',
		layer: 'imddata2',
		id: 'lsoa11cd'
	}
	const colors = ['#d0587e', '#da7b91', '#e39ca5', '#eabcb9', '#f0dccd', '#e6f5d0', '#bfe4ab', '#97d287', '#6cc064', '#37ae3f'];
	const datasets = [
		{
			code: 'lsoadata',
			source: 'https://raw.githubusercontent.com/bothness/geo-data/main/csv/imd-decile-lsoa-2019.csv'
		},
		{
			code: 'laddata',
			source: 'https://raw.githubusercontent.com/bothness/geo-data/main/csv/imd-stats-lad-2019.csv'
		},
		{
			code: 'ladtypes',
			source: 'https://raw.githubusercontent.com/bothness/geo-data/main/csv/imd-types-lad-2019.csv'
		}
	];
	const types = [
		{
			code: 'flat',
			name: 'Flat',
			description: 'Districts with a more or less equal spread of deprivation levels, from highest to lowest, across its neighbourhoods.'
		},
		{
			code: 'left',
			name: 'Skewed left',
			description: 'Districts with a greater proportion of deprived neighbourhoods.'
		},
		{
			code: 'right',
			name: 'Skewed right',
			description: 'Districts with a lesser proportion of deprived neighbourhoods.'
		},
		{
			code: 'nshape',
			name: 'N-shaped',
			description: 'Districts with more neighbourhoods towards the middle of the deprivation scale.'
		},
		{
			code: 'ushape',
			name: 'U-shaped',
			description: 'Districts with more neighbourhoods towards the extremes of the deprivation scale.'
		}
	];
	const means = [
		{
			code: 'low20',
			name: 'Most deprived',
			description: 'The 20% of districts with the highest mean neighbourhood deprivation.'
		},
		{
			code: 'high20',
			name: 'Least deprived',
			description: 'The 20% of districts with the lowest mean neighbourhood deprivation.'
		}
	];
	
	let data = {
		geojson: null,
		lsoadata: null,
		laddata: null,
		ladtypes: null,
		ladlookup: null,
		typecodes: {},
		meancodes: {}
	};
	
	let highlight = {
		hovered: null,
		selected: null,
		multi: null,
		type: null,
		mean: null
	};
	
	// Load topojson data to geojson object
	getTopo(topojson.url, topojson.layer)
	.then(result => {
		// Filter for only English LADs
		let features = result.features.filter(f => f.properties[topojson.id].substr(0,1) == 'E');
		data.geojson = result;
		data.geojson.features = features;
	});
	
	// Load data
	for (let i in datasets) {
		getData(datasets[i].source)
		.then(result => {
			data[datasets[i].code] = result;
			return result;
		})
		.then(result => {
			if (datasets[i].code == 'laddata') {
				// Get codes for each typology
				for (let i in types) {
					data.typecodes[types[i].code] = result.filter(d => d.type == types[i].code).map(d => d.code);
				}

				// Get codes for most and least deprived areas
				let sorted = [...result];
				sorted = sorted.sort((a, b) => a.mean - b.mean).map(d => d.code);
				data.meancodes['low20'] = sorted.slice(0, Math.floor(sorted.length * 0.2));
				data.meancodes['high20'] = sorted.slice(Math.floor(sorted.length * 0.8), sorted.length);
				console.log(data.meancodes);

				// Duplicate data in indexed lookup format
				let lookup = {};
				for (let i in result) {
					lookup[result[i].code] = result[i];
				}
				data.ladlookup = lookup;
			} else if (datasets[i].code == 'ladtypes') {
				let lookup = {};
				for (let i in result) {
					result[i].description = result[i].code ? types.filter(d => d.code == result[i].code)[0].description : null;
					lookup[result[i].code] = result[i];
				}
				data[datasets[i].code] = lookup;
			}
		});
	}
	
	function selectType() {
		highlight.multi = highlight.type ? data.typecodes[highlight.type] : [];
		highlight.mean = null;
		highlight.selected = null;
	}

	function selectMean() {
		highlight.multi = highlight.mean ? data.meancodes[highlight.mean] : [];
		highlight.type = null;
		highlight.selected = null;
	}
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
		background-color: #111;
		color: #bbb;
	}
	:global(h1, h2) {
		color: #eee;
	}
	main {
		padding: 30px;
	}
	select {
		width: 250px;
	}
	.right {
		float: right;
	}
</style>

<Panel>
	<main>
		<h1>Deprivation Analysis</h1>
		{#if data.laddata}
		<!-- svelte-ignore a11y-no-onchange -->
		<select bind:value={highlight.type} on:change={selectType} >
			<option value={null}>
				Select by distribution
			</option>
			{#each types as type}
			<option value={type.code}>
				{type.name}
			</option>
			{/each}
		</select><br />
		<!-- svelte-ignore a11y-no-onchange -->
		<select bind:value={highlight.mean} on:change={selectMean} >
			<option value={null}>
				Select by deprivation
			</option>
			{#each means as mean}
			<option value={mean.code}>
				{mean.name}
			</option>
			{/each}
		</select><br />
		<!-- svelte-ignore a11y-no-onchange -->
		<select bind:value={highlight.selected} >
			<option value={null}>
				Select district
			</option>
			{#each data.laddata.sort((a, b) => a.name.localeCompare(b.name)) as lad}
			<option value={lad.code}>
				{lad.name}
			</option>
			{/each}
		</select>
		{/if}
		{#if highlight.selected && data.ladlookup}
		<h2>{data.ladlookup[highlight.selected].name}</h2>
		<ColChart data="{[
			{decile: '1', value: data.ladlookup[highlight.selected]['1'], color: colors[0]},
			{decile: '2', value: data.ladlookup[highlight.selected]['2'], color: colors[1]},
			{decile: '3', value: data.ladlookup[highlight.selected]['3'], color: colors[2]},
			{decile: '4', value: data.ladlookup[highlight.selected]['4'], color: colors[3]},
			{decile: '5', value: data.ladlookup[highlight.selected]['5'], color: colors[4]},
			{decile: '6', value: data.ladlookup[highlight.selected]['6'], color: colors[5]},
			{decile: '7', value: data.ladlookup[highlight.selected]['7'], color: colors[6]},
			{decile: '8', value: data.ladlookup[highlight.selected]['8'], color: colors[7]},
			{decile: '9', value: data.ladlookup[highlight.selected]['9'], color: colors[8]},
			{decile: '10', value: data.ladlookup[highlight.selected]['10'], color: colors[9]}
		]}" yMax={30} />
		<div>
			<small>&larr; More deprived</small>
			<small class="right">Less deprived &rarr;</small>
		</div>
		<p><small>
			Type: <strong>{data.ladtypes[data.ladlookup[highlight.selected].type].name}</strong>&nbsp;&nbsp;
			Mean: <strong>{data.ladlookup[highlight.selected].mean.toFixed(2)}</strong>&nbsp;&nbsp;
			Std Dev: <strong>{data.ladlookup[highlight.selected].sdev.toFixed(2)}</strong></small></p>
		{:else if highlight.type && data.ladtypes}
		<h2>{data.ladtypes[highlight.type].name}</h2>
		<p>{data.ladtypes[highlight.type].description}</p>
		<ColChart data="{[
			{decile: '1', value: data.ladtypes[highlight.type]['1'], color: colors[0]},
			{decile: '2', value: data.ladtypes[highlight.type]['2'], color: colors[1]},
			{decile: '3', value: data.ladtypes[highlight.type]['3'], color: colors[2]},
			{decile: '4', value: data.ladtypes[highlight.type]['4'], color: colors[3]},
			{decile: '5', value: data.ladtypes[highlight.type]['5'], color: colors[4]},
			{decile: '6', value: data.ladtypes[highlight.type]['6'], color: colors[5]},
			{decile: '7', value: data.ladtypes[highlight.type]['7'], color: colors[6]},
			{decile: '8', value: data.ladtypes[highlight.type]['8'], color: colors[7]},
			{decile: '9', value: data.ladtypes[highlight.type]['9'], color: colors[8]},
			{decile: '10', value: data.ladtypes[highlight.type]['10'], color: colors[9]}
		]}" yMax={30} />
		<div>
			<small>&larr; More deprived</small>
			<small class="right">Less deprived &rarr;</small>
		</div>
		{:else if highlight.mean}
		<h2>{means.filter(d => d.code == highlight.mean)[0].name}</h2>
		<p>{means.filter(d => d.code == highlight.mean)[0].description}</p>
		{/if}
		<p><small>Data derived from the 2019 Index of Multiple Deprivation for LSOAs in England.</small></p>
	</main>
</Panel>

<Map style={mapstyle} minzoom={5} maxzoom={13}>
	{#if data.geojson}
	<MapSource id="lad" type="geojson" data={data.geojson} promoteId={topojson.id}>
		<MapLayer id="fill" source="lad" type="fill" bind:hovered={highlight.hovered} bind:selected={highlight.selected} paint="{{'fill-color': 'rgba(255, 255, 255, 0)'}}" click={true} hover={true} />
		<MapLayer id="line" source="lad" type="line" highlight={true} highlighted={highlight.multi} paint="{{
			'line-color': ['case',
				['==', ['feature-state','selected'], true], 'orange',
				['==', ['feature-state','hovered'], true], 'rgb(192, 192, 192)',
				['==', ['feature-state','highlighted'], true], 'rgb(128, 192, 192)',
				'rgb(128, 128, 128)'
			],
			'line-width': ['case',
				['==', ['feature-state','selected'], true], 2,
				['==', ['feature-state','hovered'], true], 1.5,
				['==', ['feature-state','highlighted'], true], 1,
				0.3
			]
		}}" />
		<MapLayer id="mask" source="lad" type="fill" paint="{{
			'fill-color': ['case',
				['==', ['feature-state','selected'], true], 'rgba(0, 0, 0, 0)',
				['==', ['feature-state','hovered'], true], 'rgba(0, 0, 0, 0)',
				['==', ['feature-state','highlighted'], true], 'rgba(0, 0, 0, 0.5)',
				'rgba(72, 72, 72, 0.5)'
			]
		}}" order="aeroway-taxiway" />
	</MapSource>
	{/if}
	{#if data.lsoadata}
	<MapSource id="lsoa" type="vector" url={vector.url} layer={vector.layer} promoteId={vector.id} minzoom={5} >
		<MapLayer id="lsoa" source="lsoa" sourceLayer={vector.layer} data={data.lsoadata} type="fill" paint="{{
			'fill-color': ['case',
				['!=', ['feature-state','color'], null], ['feature-state','color'],
				'rgba(255, 255, 255, 0)'
			]
		}}" {colors} order="building" />
	</MapSource>
	{/if}
</Map>